'use client';

import Link from 'next/link';
import Image from 'next/image';
import { usePathname } from 'next/navigation';
import { useEffect, useState } from 'react';

const Sidebar = () => {
  const pathname = usePathname();
  const [isClient, setIsClient] = useState(false);

  useEffect(() => {
    setIsClient(true);
  }, []);

  const menuItems = [
    { href: '/', label: 'main', address: '0x00' },
    { href: '/profile', label: 'profile', address: '0x01' },
    { href: '/projects', label: 'projects', address: '0x02' },
    { href: '/portfolio', label: 'portfolio', address: '0x03' },
    { href: '/contact', label: 'contact', address: '0x04' },
  ];

  return (
    <aside className="w-64 min-h-screen p-6 font-mono text-sm border-r" style={{
      backgroundColor: '#2e3440', // Terminal background (Nord theme)
      color: '#eceff4', // Terminal foreground
      borderColor: '#4c566a'
    }}>
      <div className="mb-8">
        <Link href="/?page=1">
          <Image 
            src="https://www.shidare.com/images/shidare_dot_com.png" 
            alt="shidare.com logo" 
            width={120}
            height={32}
            className="h-8 w-auto mb-4"
            style={{ cursor: 'pointer' }}
          />
        </Link>
        <div className="text-xs" style={{ color: '#88c0d0' }}>
          ;-- dreams of unicorns:
        </div>
      </div>

      <nav>
        <ul className="space-y-2">
          {menuItems.map((item) => {
            const isActive = isClient && pathname === item.href;
            return (
              <li key={item.href}>
                <Link
                  href={item.href}
                  className={`block py-2 px-3 rounded transition-colors sidebar-link ${
                    isActive ? 'border-l-2' : ''
                  }`}
                  style={{
                    backgroundColor: isActive ? '#434c5e' : 'transparent',
                    color: isActive ? '#d8dee9' : '#eceff4',
                    borderLeftColor: isActive ? '#88c0d0' : 'transparent'
                  }}
                  suppressHydrationWarning
                >
                  <span style={{ color: '#7f9f7f' }}>{item.address};</span>{' '}
                  <span style={{ color: isActive ? '#d8dee9' : '#eceff4' }}>{item.label}</span>
                </Link>
              </li>
            );
          })}
        </ul>
      </nav>
    </aside>
  );
};

export default Sidebar; 